@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.card {
  background-color: var(--white);
  border-top: 3px solid var(--gray-200);
  width: 100%; }
  .card .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 15px;
    border-bottom: 1px solid var(--gray-200);
    font-size: 12px;
    font-weight: 500; }
    .card .card-header .action-buttons .anticon {
      margin: 0 5px;
      cursor: pointer; }
      .card .card-header .action-buttons .anticon:last-child {
        margin-right: 0; }
  .card .card-body {
    padding: 0;
    max-height: 0;
    overflow: hidden;
    -webkit-transition: padding 0.2s ease 0.1s, max-height 0.1s ease;
    transition: padding 0.2s ease 0.1s, max-height 0.1s ease; }
    .card .card-body.is-open {
      padding: 20px;
      max-height: 100000px;
      -webkit-transition: padding 0.2s ease 0s, max-height 0.1s ease;
      transition: padding 0.2s ease 0s, max-height 0.1s ease; }


.label {
