.row {
  display: flex;
  margin: 30px 0;
}

@for $i from 1 through 12 {
  .col-#{$i} {
    width: calc(100% * (#{$i} / 12));
    margin: 0 10px;

    > .row:first-child {
      margin: 0;
    }

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
